.full-screen {
  height: auto;
  min-height: calc(100vh - 100px);
  padding-top: 30px;
  display: flex;
  align-items: flex-start;
  background: url("../img/content/full_bg.jpg");
  background-size: cover;
  background-position: center;
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .full-screen {
    display: block;
  }
}

.TextInput {
  display: block;
  width: 100%;
  border: none;
  border: 1px solid #e5e5e5;
  padding: 2px 4px;
  font-weight: 300;
  color: #333333;
  height: 40px;
  outline: none;
  margin: 0 0 20px 0;
}

.TextInput_error {
  border: 1px solid #ea1f10;
  padding: 2px 4px;
}

.custom-checbox {
  position: relative;
  cursor: pointer;
  align-self: center;
  display: inline-block;
}

.custom-checbox input {
  display: none;
}

.custom-checbox.checkbox__title {
  padding-left: 30px;
}

.custom-checbox input:disabled ~ .checkbox__title, .custom-checbox input:disabled ~ .radio__title {
  cursor: default;
}

.custom-checbox input:disabled ~ .checkbox__title:before, .custom-checbox input:disabled ~ .radio__title:before {
  opacity: .9;
  background: #eee;
}

.custom-checbox input:checked ~ .checkbox__title:before {
  background: url("../img/minified-svg/checked.svg");
  background-position: center center;
  background-size: 60%;
  background-repeat: no-repeat;
  text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.05);
}

.custom-checbox .checkbox__title:before, .custom-checbox .radio__title:before {
  content: '';
  width: 22px;
  height: 22px;
  line-height: 20px;
  background: #fff;
  position: absolute;
  left: 0;
  top: 2px;
  border: 1px solid #dbdbdb;
  box-sizing: border-box;
  text-align: center;
}

.checkbox__title {
  -moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;
  padding-left: 30px;
}

.btn {
  -moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;
  width: 100%;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  height: 40px;
  line-height: 0;
}

.btn:active, .btn:visited, .btn:focus {
  outline: none;
}

.btn_orange {
  background: #ff8041;
  color: #fff;
}

.btn_orange:hover {
  background: #de8358;
}

@media only screen and (min-width: 768px) {
  .full-screen {
    align-items: center;
    justify-content: center;
  }
}

.footer {
  background: #000;
  color: #fff;
  height: 50px;
}

@media only screen and (min-width: 768px) {
  .form-wrapper {
    max-width: 600px;
    flex-basis: 600px;
  }
  .inline-inputs {
    display: flex;
  }
  .form-wrapper__ind {
    text-align: center;
  }
  .form-wrapper__ind .inline-inputs {
    display: flex;
    justify-content: space-between;
